/* Fonts */
@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');

/* Variables */
:root{
    --nav-width: 92px;

    /* Colors */
    --first-color: #2981D5;
    --bg-color: #3227AB;
    --white-color: #F4F4F4;
    --sub-color: #FF9D09;

    /* Fonts */
    --body-font: 'Mulish', sans-serif;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;

    /*z z index */
    --z-fixed: 100;
}

/* Base */
*,::before,::after{
    box-sizing: border-box;
}
body{
    position: relative;
    margin: 0;
    padding: 2rem 0 0 6.75rem;
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    transition: .5s;
}
h1{
    margin: 0;
}
ul{
    margin: 0;
    padding: 0;
    list-style: none;
}
a{
    text-decoration: none;
}

/* l nav */
.l-navbar{
    position: fixed;
    top: 0;
    left: 0;
    width: var(--nav-width);
    height: 100vh;
    background-color: var(--bg-color);
    color: var(--white-color);
    padding: 1.5rem 1.5rem 2rem;
    transition: .5s;
    z-index: var(--z-fixed);
}

/* nav */
.nav{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;

    &__brand{
        display: grid;
    grid-template-columns: max-content max-content;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    }

    &__toggle{
        font-size: 1.25rem;
        padding: .75rem;
        cursor: pointer;
    }

    &__logo{
        color: var(--white-color);
        font-weight: 600;
    }

    &__link{
        display: grid;
        grid-template-columns: max-content max-content;
        align-items: center;
        column-gap: .75rem;
        padding: .75rem;
        color: var(--white-color);
        border-radius: .5rem;
        margin-bottom: 1rem;
        transition: .3s;
        cursor: pointer;

        &:hover{
            background-color: var(--first-color);
        }
    }
}

